@import url('https://fonts.googleapis.com/css2?family=Caramel&family=Roboto:wght@400;500;700&family=Rubik+Vinyl&display=swap'); 
/* font-family: 'Caramel', cursive;
font-family: 'Roboto', sans-serif;
font-family: 'Rubik Vinyl', cursive; */

*{
  margin: 0;
  padding: 0;

}
body{
  background-color: #fff;
  color: #222;
  font-weight: 300;
  font-family: 'Roboto', sans-serif;
}
.wrapper{
  width: 1280px;
  margin: 50px auto;
}
header .logo{
  font-family: 'Rubik Vinyl', cursive; 
  font-weight: 600;
  font-size: 30px;
}
header{
  position: relative;
}
header ul.nav{
  float: right;
  list-style: none;

}
header ul.nav li{
  display: inline;
  margin-left: 25px;
  cursor: pointer;
  transition: opacity 500ms;
}
header ul.nav li:hover{
  opacity: 0.5;
}

header .presentation{
  margin:  50px 0;
  background: url('./img/foto2.jpg') no-repeat; 
  width: 100%;
  height: 450px;
  background-size: cover;
  background-position: center center;
  background-blend-mode: multiply;
  background-color: #bcbcbc;
  position: relative;
}
header .presentation::after{
  font-family: 'Caramel', cursive; 
 
  content: "Найкращі товари для вашої краси";
  position: absolute;
  top: 100px;
  left: 50px;
  width: 300px;
  font-size: 35px;
  font-weight: 700;
  color: #fff;

}

header .presentation::before{
  font-family: 'Caramel', cursive;
  content: "Прикраси для прекрасних.";
  position: absolute;
  top: 260px;
  left: 50px;
  width: 300px;
  font-size: 20px;
  font-weight: 300;
  color: #fff;
}
header .shop-cart-button{
  float: right;
  cursor: pointer;
  transition: color, transform 500ms ease;
}

header .shop-cart-button:hover,
header .shop-cart-button:active
{
  color: #dc3d3d;
  transform: scale(1.5);
}
header .shop-cart{
  position: absolute;
  top: 30px;
  right: 0px;
  width: 450px;
  background: #fafafa;
  -webkit-box-shadow: 4px 5px 15px -7px #606060;
  box-shadow: 4px 5px 15px -7px #606060;
  z-index: 100;
  padding: 20px;
  padding-bottom: 0px;
}
header .shop-cart .empty{
  font-size: 20px;
  margin-bottom: 20px;
}
header .shop-cart .item{
  width: 100%;
  float: left;
  margin-bottom: 20px;

}
header .shop-cart .item img{
  width: 70px;
  float: left;
  margin-right: 20px;
}
header .shop-cart .item h2{
  font-size: 20px;
  margin-bottom: 10px;
}
header .shop-cart .item p{
  color: #797979;
  font-weight: 600;

}
header .shop-cart .item .delet-icon{
  color: #797979;
  float: right;
  position: relative;
  top: -25px;
  cursor: pointer;
  transition: color, transform 500ms ease;
}
header .shop-cart .item .delet-icon:hover{
  color: #dc3d3d;
  transform: scale(1.5);
}
header .shop-cart .summa{
  float: left;
  width: 100%;
  font-weight: 600;
  font-size: 20px;
  margin-bottom: 20px;
}
.categories div{
  display: inline-block;
  background: #f2f2f2;
  border-radius: 50px;
  padding: 10px 20px;
  margin-bottom: 25px;
  margin-right: 15px;
  cursor: pointer;
  border: 1px solid transparent;
  transition: all 500ms ease;
}
.categories div:hover{
  border-color: silver;
  transform: scale(1.2);
}
.full-item{
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgb(0,0,0,0.8);
  z-index: 1000;
  overflow: auto;
}
.full-item > div{
  width: 600px;
  position: relative;
  margin: 10% auto;
  padding: 40px 30px;
  background: #fff;
  border-radius: 9px;
  
}
main{
  display: flex;
  width: 100%;
  flex-wrap: wrap;
  justify-content: space-between;
}
main .item{
  overflow: hidden;
  width: 30%;
  margin-bottom: 100px;
  background: #f5f5f5;
  position: relative;
  padding-bottom: 20px;
  border-radius: 10px 10px 0px 0px;

}
main .item img, .full-item img{
  width: 100%;
  transition: transform 500ms;
  cursor: pointer;
}
main .item img:hover, .full-item img:hover{
  transform: scale(1.05);
}
main h2, .full-item h2,
main p, .full-item p,
main b,
.full-item b{
  margin: 10px 20px;
  color: #333;
}
main b,
.full-item b{
  color: #5fa36a;
}
main .add-to-cart,
.full-item .add-to-cart{
  position: absolute;
  right: 20px;
  bottom: 20px;
  background: #ca5252;
  width: 35px;
  height: 35px;
  text-align: center;
  line-height: 35px;
  color: #fff;
  border-radius: 50%;
  font-weight: 600;
  transition: transform 500ms;
  cursor: pointer;
}
main .add-to-cart:hover,
.full-item .add-to-cart:hover{
  transform: scale(1.05) translateY(-5px);
}
footer{
  text-align: center;
  margin-top: 50px;
}
